<template>
  <div class="order-content">
    <div class="order-top-title">
      <p :class="{'titleChecked':status==-1}" @click="status=-1">全部订单</p>
      <p :class="{'titleChecked':status==0}" @click="status=0">赠送中</p>
      <p :class="{'titleChecked':status==1}" @click="status=1">待发出</p>
      <p :class="{'titleChecked':status==2}" @click="status=2">待收货</p>
    </div>
    <V-Scroll :isPullDown="false" ref="a" :autoLoad="false" :pulldown="orderFun" :on-scroll-bottom="orderFun">
    <!--<V-Scroll :scroll-height="`${mainHeight}px`">-->
      <orderlist :dataList = orderLists ></orderlist>
    </V-Scroll>
  </div>
</template>

<script>
import Ajax from "@/mixins/ajax";
import orderlist from "~/orderList";

// 全部
const orderUserListUrl = "/order/list/userList";
// 赠送中
const goodsUserListUrl = "/goods/list/userList";

export default {
  name: "orderList",
  data() {
    return {
      status: -1,
      page: 1,
      orderLists: [],
      orderParams: {
        startPage: 1,
        pageSize: 10,
        status: -1,
        sellerUserId: ""
      },
      giveList: {
        userId: "",
        status: 2,
        startPage: 1,
        pageSize: 10,
        type: 0
      }
    };
  },
  mixins: [Ajax],
  components: {
    orderlist
  },
  watch: {
    status(nextValue) {
      this.page = 1;
      if (nextValue === 0) {
        this.giveList.startPage = this.page;
      } else {
        this.orderParams.startPage = this.page;
      }
      this.orderFun();
    }
  },
  methods: {
    orderFun() {
      if (this.status === 0) {
        // 赠送中
        this.get(goodsUserListUrl, this.giveList).then(body => {
          console.log(body);
        });
      } else {
        // 其他
        this.get(orderUserListUrl, this.orderParams).then(body => {
          console.log(body);
        });
      }
    }
  },
  mounted() {
    this.status = this.$route.params.status;
  }
};
</script>

<style scoped>
  @import "../../style/order.css";
</style>




