        </div>
      </li>
    </ul>
  </div>
</template>

<script>
import Ajax from "@/mixins/ajax";

export default {
  name: "orderList",
  pror: {
    dataList: {
      type: Array,
      default: () => []
    }
  },
  data() {
    return {
      status: -1
    };
  },
  mixins: [Ajax],
  components: {},
  mounted() {
  },
  methods: {}
};
</script>

<style scoped>
  @import "../../style/order.css";
</style>




